<template>
  <div class="AuthenticationConcept padding students">
    <!-- 认证理念 -->
    <div class="lop">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/student/AuthenticationConcept' }">信息公开</el-breadcrumb-item>
        <el-breadcrumb-item>
          认证理念
        </el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="AuthenticationConcept-dv">
        <img src="@/assets/mao.png" alt="">
        <span>认证理念</span>
    </div>
    <div class="Concept-ll" v-for="(item, index) in datalist" :key="index">
      <div>
          <img src="@/assets/xinghao.png" alt="" style="position: absolute;right: -6px;top: -6px;">
      </div>
      <div class="Concept-pp">{{item.title}}</div>
      <div  class="Concept-dv" v-html="item.content">
           
      </div>
      <!-- <div class="Concept-dv1">
          <button>上一页</button>
          <button>下一页</button>
      </div> -->
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      datalist:[]
    };
  },
  methods:{
    ///information/information/getCertification
    getCertification(){
      this.$get('/info/information/getCertification').then(res=>{
        console.log(res,'res')
        this.datalist = res
      })
    }
  },
  mounted(){
    this.getCertification()
  }
};
</script>
<style lang="scss" scoped>
.AuthenticationConcept {
  .AuthenticationConcept-dv{
    padding-top: 10px;
    overflow: hidden;
    padding-bottom: 20px;
    // background-color: #fff;
    img{
      float: left;
      margin-top: 3px;
      margin-right: 6px;
    }
    span{
      float: left;
    }
  }
  .lop{
    box-shadow: 4px -4px 10px rgb(187, 184, 184); ;
    background-color: rgba(255, 255, 255, 1);
    margin-top: 1px;
  }
  text-align: left;
  // box-shadow: 4px -4px 10px rgb(187, 184, 184); ;
  // width: 80%;
  .Concept-ll{
    position: relative;
      background-color: rgba(255, 255, 255, 1);
      box-shadow: 5px 10px 10px #e9e8e8; 
  }
  .Concept-dv{
      margin-top: 18px;
      padding: 0 20px;
      padding-bottom: 50px;
  }
  .Concept-pp{
    padding: 20px;
    border-bottom: 1px solid rgb(243, 240, 240);
    font-weight: 700;
    font-size: 18px;
  }
    .Concept-dv1{
        text-align: center;
        margin-top: 30px;
        padding-bottom: 20px;
        button{
            width: 90px;
            height: 30px;
            background-color: rgb(50, 104, 247);
            color: #fff;
            //消除button的默认样式
            margin: 0;
            padding: 0;
            border: 1px solid transparent;  //自定义边框
            outline: none;    //消除默认点击蓝色边框效果
        }
        button:nth-child(1){
            border-top-left-radius: 14px;
            border-bottom-left-radius: 14px;
        }
        button:nth-child(2){
            border-top-right-radius: 14px;
            border-bottom-right-radius: 14px;
        }
    }
}
</style>




